@media only screen and (max-width: 767px)
{
	.top_bar
	{
		display: none;
	}
	.header.scrolled
	{
		top: 0px;
	}
	.header.scrolled .header_content
	{
		height: 60px;
	}
	.blog_category_col:not(:nth-last-child(-n+3))
	{
		margin-bottom: 0px;
	}
	.blog_category_col:not(:last-child)
	{
		margin-bottom: 30px;
	}
	.blog_image
	{
		height: 330px;
	}
}

/************
11. 575px
************/

@media only screen and (max-width: 575px)
{
	h1{font-size: 28px;}
	h2{font-size: 24px;}
	p{font-size: 13px;}
	.header_content
	{
		height: 70px;
	}
	.logo_text
	{
		font-size: 20px;
	}
	.menu
	{
		width: 100%;
		right: -100%;
		padding-right: 20px;
		padding-left: 20px;
		padding-top: 75px;
	}
	.menu_close_container
	{
		right: 18px;
	}
	.menu .search
	{
		margin-bottom: 40px;
	}
	.menu_extra
	{
		right: 20px;
		bottom: 10px;
	}
	.menu_phone, .menu_social
	{
		font-size: 10px;
	}
	.menu_social
	{
		margin-top: 0;
	}
	.home
	{
		height: 330px;
	}
	.home_content
	{
		top: 60%;
		-webkit-transform: translateY(-50%);
		transform: translateY(-50%);
	}
	.home_title
	{
		font-size: 24px;
	}
	.home_button
	{
		width: 185px;
		height: 36px;
		margin-top: 28px;
	}
	.home_button a
	{
		line-height: 36px;
		font-size: 10px;
		font-size: 10px;
	}
	.course_body,
	.course_footer
	{
		padding-left: 15px;
		padding-right: 15px;
	}
	.course_title a
	{
		font-size: 16px;
	}
	.course_info ul li a
	{
		font-size: 13px;
	}
	.instructor_image_container
	{
		width: 120px;
		height: 120px;
		border: solid 8px #ffffff;
	}
	.instructor_image
	{
		top: -8px;
		left: -8px;
		width: 120px;
		height: 120px;
	}
	.register_form_container
	{
		padding-left: 15px;
		padding-right: 15px;
	}
	.register_timer_title,
	.timer_num
	{
		font-size: 24px;
	}
	.form_button
	{
		height: 36px;
		font-size: 10px;
	}
	.timer_list li:not(:last-child)
	{
		margin-right: 12px;
	}
	.event_date
	{
		width: 60px;
		height: 60px;
	}
	.event_day
	{
		font-size: 24px;
	}
	.event_month
	{
		font-size: 9px;
		margin-top: 8px;
	}
	.event_body
	{
		padding-left: 15px;
		padding-right: 15px;
	}
	.event_title a
	{
		font-size: 16px;
	}
	.blog_title
	{
		font-size: 24px;
	}
	.blog_title_container
	{
		top: calc(100% - 105px);
		padding-left: 20px;
		padding-right: 15px;
	}
	.blog_right_title a
	{
		font-size: 24px;
	}
	.blog_right_text p
	{
		font-size: 13px;
	}
}

